import React, { createContext, useState, useEffect, useCallback } from 'react';
import { io } from 'socket.io-client';

//  URL  
const BACKEND_URL = 'https://muksa-qian.onrender.com';

//   
const SocketContext = createContext({
  socket: null,
  isConnected: false,
  socketId: null,
  connect: () => {},
  disconnect: () => {},
  reconnect: () => {},
  setSocket: () => {}
});

//   
export const SocketProvider = ({ children }) => {
  const [socket, setSocketState] = useState(null);
  const [isConnected, setIsConnected] = useState(false);
  const [socketId, setSocketId] = useState(null);
  
  //    
  useEffect(() => {
    //     
    const socketInstance = io(BACKEND_URL, {
      withCredentials: true,
      transports: ['websocket', 'polling'],
      reconnection: true,
      reconnectionAttempts: 5,
      reconnectionDelay: 1000,
      timeout: 20000
    });

    //   
    const handleConnect = () => {
      console.log('[Socket] :', socketInstance.id);
      setIsConnected(true);
      setSocketId(socketInstance.id);
    };

    //    
    const handleConnectError = (error) => {
      console.error('[Socket]  :', error);
      setIsConnected(false);
    };

    //    
    const handleDisconnect = (reason) => {
      console.log('[Socket]  :', reason);
      setIsConnected(false);
      
      //    (    )
      if (reason === 'io server disconnect' || reason === 'transport close') {
        console.log('[Socket]   ...');
        setTimeout(() => {
          socketInstance.connect();
        }, 2000);
      }
    };

    //   
    const handleReconnect = (attemptNumber) => {
      console.log(`[Socket]   (${attemptNumber} )`);
      setIsConnected(true);
    };

    //    
    socketInstance.on('connect', handleConnect);
    socketInstance.on('connect_error', handleConnectError);
    socketInstance.on('disconnect', handleDisconnect);
    socketInstance.on('reconnect', handleReconnect);

    //   
    setSocketState(socketInstance);

    //     
    return () => {
      console.log('[Socket]  ');
      socketInstance.off('connect', handleConnect);
      socketInstance.off('connect_error', handleConnectError);
      socketInstance.off('disconnect', handleDisconnect);
      socketInstance.off('reconnect', handleReconnect);
      socketInstance.disconnect();
    };
  }, []);

  //    
  const connect = useCallback(() => {
    if (socket && !socket.connected) {
      console.log('[Socket]   ');
      socket.connect();
    }
  }, [socket]);

  //    
  const disconnect = useCallback(() => {
    if (socket && socket.connected) {
      console.log('[Socket]   ');
      socket.disconnect();
    }
  }, [socket]);

  //   
  const reconnect = useCallback(() => {
    if (socket) {
      console.log('[Socket]   ');
      socket.disconnect();
      setTimeout(() => {
        socket.connect();
      }, 500);
    }
  }, [socket]);

  //    ( )
  const setSocket = useCallback((newSocket) => {
    if (socket && socket !== newSocket) {
      socket.disconnect();
    }
    setSocketState(newSocket);
  }, [socket]);

  //  
  const contextValue = {
    socket,
    isConnected,
    socketId,
    connect,
    disconnect,
    reconnect,
    setSocket
  };

  return (
    <SocketContext.Provider value={contextValue}>
      {children}
    </SocketContext.Provider>
  );
};

export default SocketContext;
