import { database } from '../firebase';  //  firebase.js export database
import { ref, push, set, get, child, remove } from 'firebase/database';


//   
export async function createGameRoomInFirebase(gameName, playerName) {
  try {
    const roomsRef = ref(database, 'rooms');        // rooms  ref 
    const newRoomRef = push(roomsRef);               // rooms    
    const roomData = {
      name: gameName,
      host: playerName,
      players: [
        {
          id: 'host', //    ID 
          name: playerName,
          isHost: true,
          isReady: false
        }
      ],
      
      createdAt: Date.now()
    };
    await set(newRoomRef, roomData);                 //     
    console.log('Firebase   :', newRoomRef.key);
    return newRoomRef.key;                           //   ID 
  } catch (error) {
    console.error('Firebase   :', error);
    throw error;
  }
}

//    
export async function fetchGameRoomsFromFirebase() {
  try {
    const roomsRef = ref(database, 'rooms');         // rooms  
    const snapshot = await get(roomsRef);             // rooms  
    if (snapshot.exists()) {
      return snapshot.val();                         //   
    } else {
      return {};                                     //    
    }
  } catch (error) {
    console.error('Firebase    :', error);
    throw error;
  }
}
